import { getPostBySlug, getPostSlugs } from '@/lib/posts';
import { MDXRemote } from 'next-mdx-remote/rsc';
import { format } from 'date-fns';
import { ko } from 'date-fns/locale';
import Link from 'next/link';
import rehypeHighlight from 'rehype-highlight';
import rehypeSlug from 'rehype-slug';
import rehypeAutolinkHeadings from 'rehype-autolink-headings';

export async function generateStaticParams() {
    const slugs = getPostSlugs();
    return slugs.map((slug) => ({
        slug: slug.replace(/\.md$/, ''),
    }));
}

export async function generateMetadata({ params }: { params: Promise<{ slug: string }> }) {
    const { slug } = await params;
    const post = getPostBySlug(slug);
    return {
        title: post.title,
        description: post.description,
    };
}

export default async function BlogPost({ params }: { params: Promise<{ slug: string }> }) {
    const { slug } = await params;
    const post = getPostBySlug(slug);

    return (
        <article className="container-minimal py-12">
            {/* Header */}
            <header className="mb-14 text-center">
                <div className="flex justify-center gap-2 mb-6">
                    {post.category.map((cat) => (
                        <Link
                            key={cat}
                            href={`/blog?category=${cat}`}
                            className="text-xs font-bold uppercase tracking-widest text-[var(--primary)] hover:underline underline-offset-4"
                        >
                            {cat}
                        </Link>
                    ))}
                </div>

                <h1 className="text-4xl md:text-5xl lg:text-6xl font-extrabold mb-6 font-sans leading-tight tracking-tight text-[var(--foreground)]">
                    {post.title}
                </h1>

                <div className="flex items-center justify-center gap-4 text-sm text-[var(--foreground-secondary)] font-sans">
                    <time dateTime={post.date}>
                        {format(new Date(post.date), 'MMMM d, yyyy', { locale: ko })}
                    </time>
                    {post.readingTime && (
                        <>
                            <span className="opacity-50">/</span>
                            <span>{post.readingTime}</span>
                        </>
                    )}
                </div>
            </header>

            {/* Content */}
            <div className="prose prose-lg md:prose-xl max-w-none mx-auto
        font-serif
        prose-headings:font-sans prose-headings:font-bold prose-headings:tracking-tight
        prose-p:text-[var(--foreground)] prose-p:leading-loose
        prose-a:text-[var(--primary)] prose-a:no-underline prose-a:border-b prose-a:border-[var(--primary)] hover:prose-a:bg-[var(--primary)] hover:prose-a:text-white hover:prose-a:border-transparent prose-a:transition-all
        prose-strong:text-[var(--foreground)] prose-strong:font-bold
        prose-code:font-mono prose-code:text-[var(--primary)] prose-code:bg-[var(--surface)] prose-code:px-1.5 prose-code:py-0.5 prose-code:rounded-sm prose-code:before:content-none prose-code:after:content-none
        prose-pre:bg-[var(--surface)] prose-pre:border prose-pre:border-[var(--border)] prose-pre:rounded-lg
        prose-blockquote:border-l-4 prose-blockquote:border-[var(--primary)] prose-blockquote:bg-[var(--surface)] prose-blockquote:py-2 prose-blockquote:px-6 prose-blockquote:not-italic
        prose-img:rounded-lg prose-img:shadow-md
        prose-hr:border-[var(--border)] prose-hr:my-12
      ">
                <MDXRemote
                    source={post.content}
                    options={{
                        mdxOptions: {
                            rehypePlugins: [
                                rehypeHighlight,
                                rehypeSlug,
                                [rehypeAutolinkHeadings, { behavior: 'wrap' }],
                            ],
                        },
                    }}
                />
            </div>

            {/* Tags */}
            {post.tags.length > 0 && (
                <div className="mt-16 pt-8 border-t border-[var(--border)]">
                    <div className="flex flex-wrap gap-2 justify-center">
                        {post.tags.map((tag) => (
                            <span
                                key={tag}
                                className="px-4 py-1.5 text-sm rounded-full bg-[var(--surface)] text-[var(--foreground-secondary)] hover:text-[var(--primary)] transition-colors cursor-default font-sans"
                            >
                                #{tag}
                            </span>
                        ))}
                    </div>
                </div>
            )}

            {/* Back Link */}
            <div className="mt-12 text-center">
                <Link href="/blog" className="inline-block text-sm font-bold border-b-2 border-[var(--foreground)] hover:border-[var(--primary)] hover:text-[var(--primary)] transition-all pb-1 font-sans">
                    &larr; Back to all posts
                </Link>
            </div>
        </article>
    );
}
